@import url(https://fonts.googleapis.com/css?family=Fjalla+One|Montserrat:300,400,700,800|Open+Sans:300);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.body{background-image:linear-gradient(to right bottom, #b91eda, #a02ae0, #8234e5, #5d3be8, #1241eb);height:100vh;width:100%;margin:0;background-repeat:no-repeat;background-attachment:fixed}main{max-width:80%;margin:5% auto}.card_div{box-shadow:0 6px 6px rgba(0,0,0,0.3);transition:200ms;background:#fff}.card_div .card__title{display:flex;align-items:center;padding:30px 40px}.card_div .card__title h3{flex:0 1 200px;text-align:right;margin:0;color:#252525;font-family:sans-serif;font-weight:600;font-size:20px;text-transform:uppercase}.card_div .card__title .icon{flex:1 0 10px;background:#115dd8;color:#fff;padding:10px 10px;transition:200ms}.card_div .card__title .icon>a{color:#fff}.card_div .card__title .icon:hover{background:#252525}.card_div .card__body{padding:0 40px;display:flex;flex-flow:row no-wrap;margin-bottom:25px}.card_div .card__body>.half{box-sizing:border-box;padding:0 15px;flex:1 0 50%}.card_div .card__body .featured_text h1{margin:0;padding:0;font-weight:800;font-family:"Montserrat", sans-serif;font-size:64px;line-height:50px;color:#181e28}.card_div .card__body .featured_text p{margin:0;padding:0}.card_div .card__body .featured_text p.sub{font-family:"Montserrat", sans-serif;font-size:26px;text-transform:uppercase;color:#686e77;font-weight:300;margin-bottom:5px}.card_div .card__body .featured_text p.genres{font-family:"Fjalla One", sans-serif;color:#252525;font-size:26px}.card_div .card__body .image{padding-top:15px;width:100%}.card_div .card__body .image img{display:block;max-width:100%;height:auto}.card_div .card__body .description{margin-bottom:25px}.card_div .card__body .description p{margin:0;font-family:"Open Sans", sans-serif;font-weight:300;line-height:27px;font-size:16px;color:#555}.card_div .card__body span.stock{font-family:"Montserrat", sans-serif;font-weight:600;color:#a1cc16}.card_div .card__body .reviews span{font-family:"Open Sans", sans-serif;font-size:14px;margin-left:5px;color:#555}.card_div .card__footer{padding:30px 40px;display:flex;flex-flow:row no-wrap;align-items:center;position:relative}.card_div .card__footer::before{content:"";position:absolute;display:block;top:0;left:40px;width:calc(100% - 40px);height:3px;background:#115dd8;background:linear-gradient(to right, #115dd8 0%, #115dd8 20%, #ddd 20%, #ddd 100%)}

